.login-wrap {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	max-height: 100vh;
	min-height: 100vh;
	position: relative;

	.logo {
		text-transform: uppercase;
		font-size: 32px;
		line-height: 1;
		color: white;
		font-family: 'Logo', sans-serif;
		margin: 0;

		@include media-breakpoint-up(lg) {
			font-size: 42px;
		}

		span {
			color: orange;
		}
	}

	p {
		max-width: 300px;
	}

	.powered-by {
		position: absolute;
		right: 50px;
		bottom: 50px;
		margin: 0;
		font-size: 12px;
		line-height: 1;

		a {
			color: $orange;
		}
	}
}
