.person {
	&--wrap {
		img {
			width: 100%;
		}
	}

	&--banner {
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
		opacity: 0.2;
		overflow: hidden;
		filter: grayscale(1);

		// &:after {
		// 	content: '';
		// 	position: absolute;
		// 	top: 0;
		// 	left: 0;
		// 	height: 200px;
		// 	width: 100%;
		// 	background: rgba(black, 0.8);
		// }
	}

	&--thumb {
		overflow: hidden;
		padding-bottom: 100px;
		width: 100px;
		min-width: 100px;
		position: relative;
		border-radius: 1000px;

		height: 0;
		margin-right: 20px;

		@include media-breakpoint-up(lg) {
			padding-bottom: 200px;
			width: 200px;
			min-width: 200px;
			margin-right: 50px;
		}

		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&--details {
		padding-top: 0px;

		@include media-breakpoint-up(lg) {
			padding-top: 50px;
		}

		p {
			font-size: 10px;
			text-transform: uppercase;
			line-height: 1;
			margin-bottom: 5px;

			@include media-breakpoint-up(lg) {
				font-size: 12px;
				margin-bottom: 10px;
			}
		}
	}

	&--top {
		display: flex;
		height: 110px;
		margin-bottom: 20px;

		@include media-breakpoint-up(lg) {
			height: 200px;
			margin-bottom: 50px;
		}

		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			height: 200px;
			width: 100%;
			background: rgba(black, 0.2);
			z-index: -1;
		}
	}

	&--bio {
		p.bio {
			max-height: 88px;
			overflow: hidden;

			&.open {
				max-height: initial;
			}
		}
	}
}
