.carousel {
	max-width: 100%;
	overflow-x: auto;
	overflow-y: hidden;
	scrollbar-width: none;
	margin: 0 -20px 0 -90px;
	padding: 0 20px 0 90px;
	max-width: calc(100% + 110px);

	&::-webkit-scrollbar {
		display: none;
	}

	// &.freeze {
	// 	pointer-events: none;
	// }

	@include media-breakpoint-up(lg) {
		margin: 0 -50px;
		padding: 0 50px;
		max-width: calc(100% + 100px);
	}

	&--inner {
		display: flex;
		flex-wrap: nowrap;

		&:after {
			content: '';
			min-width: 20px;
		}

		@include media-breakpoint-up(lg) {
			&:after {
				content: '';
				min-width: 50px;
			}
		}
	}

	.card {
		width: 80px;
		min-width: 80px;

		@include media-breakpoint-up(md) {
			width: 100px;
			min-width: 100px;
		}

		@include media-breakpoint-up(lg) {
			width: 150px;
			min-width: 150px;
		}
	}
}

